___CSS_LOADER_EXPORT___.push([module.id, `:root{
    --cloudy: #192b85;
    --sunny: #f9f871;
    --greeny: #bffb89;
    --cloudyy: #3e4d98;
    --lightcloudy: #cbf2fa;
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body{
    background-color: var(--lightcloudy)
}
main{
    display: grid;
    place-items: center;
}
.wheather-today-container{
    display: flex;
    background-color: var(--cloudy);
    color: white;
    width: 90vw;
    flex-direction: column;
    padding: 1rem;
    margin-top: 2rem;
    border-radius: 0.5rem;
}
.header-today-container{
    display: flex;
    justify-content: space-between;
    margin-bottom: 0.3rem;
    align-items: center;
    
}
.city{
    font-size: 2rem;
    font-weight: bold;
}
.finder{
    width: 200px;
    position: relative;
}
.finder>input{
    width: 100%;
    padding: 0.3rem;
}
#search{
    cursor: pointer;
    position: absolute;
    height: 80%;
    right: 2px;
    text-align: center;
    top: 2px;
}
.today-info-container{
    display: grid;
    background-color: var(--cloudyy);
    margin-top: 1rem;
    grid-template-columns: 100px 100px 1fr;
    grid-template-rows: 120px;
    padding: 1rem;
    place-items: center;
    border-radius: 0.5rem;

}
.temperature{
    font-size: 3rem;
    margin-bottom: 1.5rem;
}
.inner-details-container{
    display: flex;
    margin-top: 0.6rem;
    gap: 3rem;
}
.status{
    padding: 0.3rem;
}
.perceived,
.pressure,
.wind{
    display: flex;
    flex-direction: column;
    padding: 0.2rem;
}

/* all days / second container */

.all-days-container{
    margin-top: 3rem;
    display: grid;
    background-color: var(--cloudyy);
    height: 500px;
    margin-left: 1rem;
    margin-right: 1rem;
    width: 90vw;
    grid-template-columns: 50px 1fr 50px;
    grid-template-rows: 50px 1fr 50px;
    border-radius: 0.5rem;
    margin-bottom: 2rem;
}
.date{
    display: flex;
    justify-content: space-around;
    place-items: center;
    grid-area: 1 / 2 / 2 / 3;
    background-color: var(--cloudyy);
    font-size: 0.7rem;

}
.wykres{
    grid-area: 2 / 1 / 3 / 2;
    background-color: var(--cloudyy);
    display: flex;
    flex-direction: column-reverse;
    justify-content: space-around;
    align-items: center;
    margin-top: 3rem;
}

.inside{
    grid-area: 2 / 2 / 3 / 3;
    background-color:   var(--lightcloudy);
    display: grid;
    grid-template-columns: repeat(15, 1fr);
    border-radius: 0.5rem;
}
.inside>div{
    border-right: solid 1px black;
}
.right{
    grid-area: 1 / 3 / 4 / 4;
    background-color:   var(--cloudyy);
    margin-right: 2rem;
}
.bottom{
    grid-area: 3 / 2 / 4 / 3;
    background-color: var(--cloudyy);
}
.container-days{
    display: flex;
    justify-content: space-between;
    flex-direction: column;
}
.container-days > :nth-child(1){
    text-align: center;
    margin-top: 0.5rem;
}
.temp-and-icon{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.icon-uppon-temp>img{
    display: block;
    width: 40px;
    height: 40px;
}



.err-text{
    display: none;
}
.err-text-show{
    display: block;
    position: absolute;
    left: 5px;
    color: red;
}

@media screen and (max-width: 1000px){
    .inside{
        grid-template-columns: repeat(10, 1fr);
    }
    .inside > :nth-child(11),
    .inside > :nth-child(12),
    .inside > :nth-child(13),
    .inside > :nth-child(14),
    .inside > :nth-child(15){
        display: none;
    }
    .all-days-container{
        height: 400px;
        grid-template-columns: 30px 1fr 30px;
        grid-template-rows: 30px 1fr 30px;
    }




    .today-info-container{
        grid-template-columns: 50px 50px 1fr;
        gap: 0.3rem;
    }
    .city{
        font-size: 1.5rem;
    }
    .finder{
        width: 150px;
        font-size: 0.8rem;
    }
    #iconJs{
        width: 50px;
        height: 50px;
    }
    .temperature{
        font-size: 1.8rem;
    }
    .inner-details-container{
        gap: 1rem;
    }
    .status{
    font-size: 1rem;
}
    .perceived,
    .pressure,
    .wind{
        font-size: 0.8rem;
    }
    .icon-uppon-temp>img{
    width: 30px;
    height: 30px;
}
}`, "",{"version":3,"sources":["webpack://./src/styles/style.css"],"names":[],"mappings":"AAAA;IACI,iBAAiB;IACjB,gBAAgB;IAChB,iBAAiB;IACjB,kBAAkB;IAClB,sBAAsB;AAC1B;AACA;IACI,SAAS;IACT,UAAU;IACV,sBAAsB;AAC1B;AACA;IACI;AACJ;AACA;IACI,aAAa;IACb,mBAAmB;AACvB;AACA;IACI,aAAa;IACb,+BAA+B;IAC/B,YAAY;IACZ,WAAW;IACX,sBAAsB;IACtB,aAAa;IACb,gBAAgB;IAChB,qBAAqB;AACzB;AACA;IACI,aAAa;IACb,8BAA8B;IAC9B,qBAAqB;IACrB,mBAAmB;;AAEvB;AACA;IACI,eAAe;IACf,iBAAiB;AACrB;AACA;IACI,YAAY;IACZ,kBAAkB;AACtB;AACA;IACI,WAAW;IACX,eAAe;AACnB;AACA;IACI,eAAe;IACf,kBAAkB;IAClB,WAAW;IACX,UAAU;IACV,kBAAkB;IAClB,QAAQ;AACZ;AACA;IACI,aAAa;IACb,gCAAgC;IAChC,gBAAgB;IAChB,sCAAsC;IACtC,yBAAyB;IACzB,aAAa;IACb,mBAAmB;IACnB,qBAAqB;;AAEzB;AACA;IACI,eAAe;IACf,qBAAqB;AACzB;AACA;IACI,aAAa;IACb,kBAAkB;IAClB,SAAS;AACb;AACA;IACI,eAAe;AACnB;AACA;;;IAGI,aAAa;IACb,sBAAsB;IACtB,eAAe;AACnB;;AAEA,gCAAgC;;AAEhC;IACI,gBAAgB;IAChB,aAAa;IACb,gCAAgC;IAChC,aAAa;IACb,iBAAiB;IACjB,kBAAkB;IAClB,WAAW;IACX,oCAAoC;IACpC,iCAAiC;IACjC,qBAAqB;IACrB,mBAAmB;AACvB;AACA;IACI,aAAa;IACb,6BAA6B;IAC7B,mBAAmB;IACnB,wBAAwB;IACxB,gCAAgC;IAChC,iBAAiB;;AAErB;AACA;IACI,wBAAwB;IACxB,gCAAgC;IAChC,aAAa;IACb,8BAA8B;IAC9B,6BAA6B;IAC7B,mBAAmB;IACnB,gBAAgB;AACpB;;AAEA;IACI,wBAAwB;IACxB,sCAAsC;IACtC,aAAa;IACb,sCAAsC;IACtC,qBAAqB;AACzB;AACA;IACI,6BAA6B;AACjC;AACA;IACI,wBAAwB;IACxB,kCAAkC;IAClC,kBAAkB;AACtB;AACA;IACI,wBAAwB;IACxB,gCAAgC;AACpC;AACA;IACI,aAAa;IACb,8BAA8B;IAC9B,sBAAsB;AAC1B;AACA;IACI,kBAAkB;IAClB,kBAAkB;AACtB;AACA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;AACvB;AACA;IACI,cAAc;IACd,WAAW;IACX,YAAY;AAChB;;;;AAIA;IACI,aAAa;AACjB;AACA;IACI,cAAc;IACd,kBAAkB;IAClB,SAAS;IACT,UAAU;AACd;;AAEA;IACI;QACI,sCAAsC;IAC1C;IACA;;;;;QAKI,aAAa;IACjB;IACA;QACI,aAAa;QACb,oCAAoC;QACpC,iCAAiC;IACrC;;;;;IAKA;QACI,oCAAoC;QACpC,WAAW;IACf;IACA;QACI,iBAAiB;IACrB;IACA;QACI,YAAY;QACZ,iBAAiB;IACrB;IACA;QACI,WAAW;QACX,YAAY;IAChB;IACA;QACI,iBAAiB;IACrB;IACA;QACI,SAAS;IACb;IACA;IACA,eAAe;AACnB;IACI;;;QAGI,iBAAiB;IACrB;IACA;IACA,WAAW;IACX,YAAY;AAChB;AACA","sourcesContent":[":root{\r\n    --cl